.products_container {
  padding-top: 121px;
}

.products_list {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    display: block;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    display: flex;
  }

  @media screen and (min-width: 1280px) {
    display: flex;
  }
}

.products_list__item {
  position: relative;
  margin-bottom: 0;
  text-align: center;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &__item:hover {
    transform: translateY(-15px);
  }

  @media screen and (min-width: 320px) and (max-width: 767px) {
    display: block;
    height: 598px;
    margin-top: 0;
    margin-right: auto;
    margin-left: auto;
    max-width: 100%;

    &__item:not(:last-child) {
      margin-bottom: 17px;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: calc((100% - 20px) / 3);
    height: 589px;

    &__item:not(:last-child) {
      margin-right: 20px;
    }
  }

  @media screen and (min-width: 1280px) {
    width: calc((100% - 30px) / 3);
    height: 735px;

    &__item:not(:last-child) {
      margin-right: 30px;
    }
  }
}

.products_image {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  z-index: 2;
  display: block;
  max-width: 100%;
  margin: 0;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 249px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 220px;
  }

  @media screen and (min-width: 1280px) {
    width: 335px;
  }
}

.products_content {
  position: absolute;
  bottom: 0%;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);

  border-radius: 24px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-top: 165px;
    padding-bottom: 40px;
    width: 280px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-top: 156px;
    padding-bottom: 40px;
    width: 100%;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 170px;
    padding-bottom: 50px;
    width: 100%;
  }
}

.products_background__icecream {
  background-color: $hero-back-color;
}

.products_background__icecoffee {
  background-color: $heading-back-color-contacts-one;
}

.products_background__milkshakes {
  background-color: $heading-back-color-contacts-two;
}

.title_products {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    bottom: 0%;
    display: block;
    text-align: center;
    width: 26px;
    height: 6px;
    text-align: center;
    background-image: url('https://img.icons8.com/2266EE/search');
  }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-bottom: 33px;
    margin-bottom: 30px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-bottom: 33px;
    margin-bottom: 30px;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 36px;
    margin-bottom: 32px;
  }
}

.subtitle {
  margin: 0;
  padding-bottom: 15px;
  font-family: Titan One;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
}

.title {
  margin: 0;
  padding-bottom: 31px;
  font-family: Titan One;
  font-size: 34px;
  line-height: 39px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $button-and-heading-color;
}
